<template>
  <div class="addplan">
    <div v-if="isDetailShow">
      <div class="pbtn">
        <el-button @click="goback" size="small" type="primary"> </el-button>
        <el-button
          @click="toEdit"
          size="small"
          v-if="isEditShow"
          v-show="noEdit"
          type="primary"
          :class="isCenter==2?'noeditbtn':''"
        ></el-button>
      </div>
      <div class="addcontent" :style="ddheight">
        <el-row class="box jibeninfo" :gutter="30">
          <el-col :span="12">
            <fieldset>
              <legend></legend>
              <div class="boxcontent">
                <el-form ref="basicInfo" size="small" :model="basicInfo" label-width="120px">
                  <el-form-item label="" prop="yearname">
                    <el-input v-model="basicInfo.yearname" readonly></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="leaders" class="qt">
                    <el-input v-model="basicInfo.leaders" readonly></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="projectname">
                    <el-input
                      type="textarea"
                      :autosize="{ minRows: 3}"
                      readonly
                      v-model="basicInfo.projectname"
                    ></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="timenode">
                    <el-input
                      type="textarea"
                      :autosize="{ minRows: 3}"
                      readonly
                      v-model="basicInfo.timenode"
                    ></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="superdept">
                    <el-input readonly v-model="basicInfo.superdept"></el-input>
                  </el-form-item>
                </el-form>
              </div>
            </fieldset>
          </el-col>
          <el-col :span="12">
            <fieldset>
              <legend></legend>
              <div class="boxcontent">
                <el-button
                  @click="lookProgress"
                  style="margin-bottom:15px;"
                  size="small"
                  type="primary"
                ></el-button>
                <div class="speed">
                  <div class="pbox">
                    <p class="tt"></p>
                    <div class="conbox">
                      <p>{{lastprogress}}</p>
                    </div>
                  </div>
                  <div class="pbox">
                    <p class="tt"></p>
                    <div class="conbox">
                      <p v-for="(item, index) in newReportList" :key="index">{{item.question}}</p>
                    </div>
                  </div>
                  <div class="pbox">
                    <p class="tt"></p>
                    <div class="conbox">
                      <p v-for="(item, index) in newReportList" :key="index">{{item.wenti}}</p>
                    </div>
                  </div>
                  <div class="pbox">
                    <p class="tt"></p>
                    <div class="conbox">
                      <p v-for="(item, index) in newReportList" :key="index">{{item.measure}}</p>
                    </div>
                  </div>
                  <div class="filelist">
                    <p class="tt"></p>
                    <div class="list">
                      <div>
                        <div v-for="(item, index) in newReportList" :key="index">
                          <div v-for="(sitem, kk) in item.newFiles" :key="kk">
                            <a
                              :href="sitem.url"
                              target="_blank"
                              style="text-align:left;"
                              class="filetitle"
                            >{{index+1}}. {{sitem.name}}</a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </fieldset>
          </el-col>
        </el-row>

        <!--  -->
        <el-row class="box" :gutter="30">
          <el-col :span="24">
            <fieldset style="width:100%;">
              <legend></legend>
              <div class="boxcontent">
                <el-table
                  border
                  :data="ruleList"
                  highlight-current-row
                  style="width: 100%;"
                  class="tableinfo"
                  :cell-class-name="cellClassName"
                  height="200px"
                >
                  <el-table-column prop="stagename" label=""></el-table-column>
                  <el-table-column prop="cycletype" label="">
                    <template slot-scope="scope">
                      <span v-html="ctypeFormat(scope,scope.row.cycletype)"></span>
                    </template>
                  </el-table-column>
                  <el-table-column prop="cycletime" label="">
                    <template slot-scope="scope">
                      <!--  -->
                      <p v-if="scope.row.cycletype==1">{{scope.row.cycletime}}</p>
                      <!--  -->
                      <p v-if="scope.row.cycletype==2">
                        <span v-html="weekFormat(scope,scope.row.cycletime)"></span>
                      </p>
                      <!--  -->
                      <p v-else-if="scope.row.cycletype==3">{{scope.row.cycletime}}</p>
                      <!--  -->
                      <p v-else-if="scope.row.cycletype==4">{{scope.row.cycletime}}</p>
                      <!--  -->
                      <p v-else-if="scope.row.cycletype==5">{{scope.row.cycletime}}</p>
                    </template>
                  </el-table-column>
                  <el-table-column prop="warnday" label="()"></el-table-column>
                  <el-table-column prop="starttime" label="">
                    <template slot-scope="scope">
                      <span>{{scope.row.starttime | dateformat('YYYY-MM-DD')}}</span>
                    </template>
                  </el-table-column>
                  <el-table-column prop="endtime" :formatter="enddateToTime" label=""></el-table-column>
                  <el-table-column prop="status" label="">
                    <template slot-scope="scope">
                      <div>
                        <el-tag size="medium" v-if="scope.row.status==1">
                          <i class="fa mark" style="background:#364e98;"></i>
                          <span></span>
                        </el-tag>
                        <el-tag size="medium" v-else-if="scope.row.status==2">
                          <i class="fa mark"></i>
                          <span></span>
                        </el-tag>
                        <el-tag size="medium" v-else-if="scope.row.status==0">
                          <i class="fa mark" style="background:#999;"></i>
                          <span></span>
                        </el-tag>
                        <p v-else>/</p>
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column label="">
                    <template scope="scope">
                      <el-button class="btn" @click="detailRuleBtn(scope.$index, scope.row)"></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </fieldset>
          </el-col>
        </el-row>
        <!--  -->
        <el-row class="box" :gutter="30">
          <el-col :span="12">
            <fieldset>
              <legend></legend>
              <div class="boxcontent">
                <el-table
                  border
                  :data="leadsData"
                  highlight-current-row
                  style="width: 100%;"
                  height="250px"
                  class="tableinfo dtable"
                  :cell-class-name="cellClassName"
                >
                  <el-table-column prop="deptName" label=""></el-table-column>
                  <el-table-column prop="leadername" label=""></el-table-column>
                  <el-table-column prop="agentname" label=""></el-table-column>
                  <el-table-column prop="formstatus" label="">
                    <template slot-scope="scope">
                      <div>
                        <el-tag size="small">
                          <i
                            class="fa mark"
                            v-if="scope.row.formstatus==1"
                            style="background:#fbe804;"
                          ></i>
                          <i
                            class="fa mark"
                            v-else-if="scope.row.formstatus==2"
                            style="background:#f00;"
                          ></i>
                          <i
                            class="fa mark"
                            v-else-if="scope.row.formstatus== -1"
                            style="background:#999;"
                          ></i>
                          <i class="fa mark" v-else></i>
                          <span v-html="formstatusFormat(scope,scope.row.formstatus)"></span>
                        </el-tag>
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column label="" width="120">
                    <template scope="scope">
                      <el-button class="btn" @click="handleDetail2(scope.$index, scope.row)"></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </fieldset>
          </el-col>
          <el-col :span="12">
            <fieldset>
              <legend></legend>
              <div class="boxcontent">
                <el-table
                  border
                  :data="dutysData"
                  height="250px"
                  highlight-current-row
                  style="width: 100%;"
                  class="tableinfo dtable"
                  :cell-class-name="cellClassName"
                >
                  <el-table-column prop="deptName" label=""></el-table-column>
                  <el-table-column prop="leadername" label=""></el-table-column>
                  <el-table-column prop="agentname" label=""></el-table-column>
                  <el-table-column label="" width="120">
                    <template scope="scope">
                      <el-button class="btn" @click="handleDetail3(scope.$index, scope.row)"></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </fieldset>
          </el-col>
        </el-row>
      </div>
    </div>
    <!--  -->
    <div class="history" v-if="isProgressShow">
      <p class="title"></p>
      <div class="hbtns">
        <el-button @click="returnDetail2" size="small" type="primary"> </el-button>
      </div>
      <el-table
        class="tableinfo"
        border
        :data="progressData"
        highlight-current-row
        style="width: 100%;"
        :height="histableHeight"
      >
        <el-table-column prop="reportdept" label=""></el-table-column>
        <el-table-column prop="createtime" label="">
          <template slot-scope="scope">
            <span>{{scope.row.createtime | dateformat('YYYY-MM-DD HH:mm:ss')}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="question" label=""></el-table-column>
        <el-table-column prop="wenti" label=""></el-table-column>
        <el-table-column prop="measure" label=""></el-table-column>
        <el-table-column prop="validate" label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.validate == 0" type="info" disable-transitions>
              <span></span>
            </el-tag>
            <el-tag v-else-if="scope.row.validate == 1" type="warning" disable-transitions>
              <span></span>
            </el-tag>
            <el-tag v-else type="success" disable-transitions>
              <span></span>
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="reason" label="">
          <template slot-scope="scope">
            <p v-html="nullFormat(scope,scope.row.reason)"></p>
          </template>
        </el-table-column>
        <el-table-column prop="filepath" label="">
          <template slot-scope="scope">
            <p v-if="scope.row.filepath==null || scope.row.filepath ==''"></p>
            <a
              :href="item.url"
              target="_blank"
              style="text-align:left;"
              class="filetitle"
              v-for="(item, index) in scope.row.filepath"
              :key="index"
              v-else
            >{{index+1}}. {{item.name}}</a>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="ruleVisible" width="700px" class="rule-dialog">
      <el-form
        :model="addRuleForm"
        :inline="true"
        label-width="93px"
        ref="addRuleForm"
        :append-to-body="true"
      >
        <el-form-item label="" prop="stagename">
          <el-input v-model="addRuleForm.stagename" readonly placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" class="day" prop="warnday">
          <el-input readonly v-model="addRuleForm.warnday">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="cycletype">
          <el-select
            v-model="addRuleForm.cycletype"
            clearable
            disabled
            placeholder=""
            @change="cycleChange()"
          >
            <el-option
              v-for="item in submitType"
              :key="item.value"
              :label="item.type"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>

        <!-- 1  -->
        <el-form-item label="" class="day" prop="region" v-if="dateShow1">
          <el-input v-model="region" readonly>
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <!-- 2  -->
        <el-form-item label="" prop="weekday" v-if="dateShow2">
          <el-select v-model="weekday" disabled clearable placeholder="">
            <el-option
              v-for="item in weekList"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 3  -->
        <el-form-item label="" prop="monthday" v-if="dateShow3">
          <el-select v-model="monthday" disabled clearable placeholder="">
            <el-option
              v-for="item in timeday"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 4  -->
        <el-form-item label="" prop="quarter" v-if="dateShow4">
          <el-select v-model="quarter" disabled clearable placeholder="">
            <el-option
              v-for="item in timeday"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 5  -->
        <el-form-item label="" prop="daytime" v-if="dateShow5">
          <el-date-picker
            v-model="daytime"
            format="yyyy-MM-dd"
            type="date"
            readonly
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>

        <el-form-item
          label=""
          prop="starttime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="addRuleForm.starttime"
            format="yyyy-MM-dd"
            type="date"
            readonly
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label=""
          prop="endtime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="addRuleForm.endtime"
            format="yyyy-MM-dd"
            type="date"
            readonly
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="ruleVisible=false" type="primary">{{ruleBtn}}</el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="taskTitle"
      :visible.sync="addLeadVisible"
      width="800px"
      class="task-dialog task-dialog2"
    >
      <el-form
        :inline="true"
        :model="addLeadData"
        label-width="100px"
        ref="addLeadData"
        :append-to-body="true"
      >
        <div class="leabox">
          <el-form-item label="" prop="deptName">
            <el-input v-model="addLeadData.deptName"></el-input>
          </el-form-item>
        </div>
        <el-form-item label="" prop="leadername">
          <el-input v-model="addLeadData.leadername"></el-input>
        </el-form-item>
        <el-form-item label="" prop="leaderphone">
          <el-input v-model="addLeadData.leaderphone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentname">
          <el-input v-model="addLeadData.agentname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentphone">
          <el-input v-model="addLeadData.agentphone"></el-input>
        </el-form-item>
        <div class="leabox leabox2">
          <el-form-item label="" prop="duty">
            <el-input type="textarea" :autosize="{ minRows: 5}" v-model="addLeadData.duty"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitLead" type="primary">{{changeWord}}</el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="taskTitle"
      :visible.sync="addDutyVisible"
      width="800px"
      class="task-dialog task-dialog2"
    >
      <el-form
        :inline="true"
        :model="addDutyData"
        label-width="100px"
        ref="addDutyData"
        :append-to-body="true"
      >
        <div class="leabox">
          <el-form-item label="" prop="deptName">
            <el-input v-model="addDutyData.deptName"></el-input>
          </el-form-item>
        </div>
        <el-form-item label="" prop="leadername">
          <el-input v-model="addDutyData.leadername"></el-input>
        </el-form-item>
        <el-form-item label="" prop="leaderphone">
          <el-input v-model="addDutyData.leaderphone" :readonly="readOn"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentname">
          <el-input v-model="addDutyData.agentname" :readonly="readOn"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentphone">
          <el-input v-model="addDutyData.agentphone" :readonly="readOn"></el-input>
        </el-form-item>
        <div class="leabox leabox2">
          <el-form-item label="" prop="duty">
            <el-input
              type="textarea"
              :autosize="{ minRows: 5}"
              :readonly="readOn"
              v-model="addDutyData.duty"
            ></el-input>
          </el-form-item>
        </div>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="submitDuty" type="primary">{{changeWord}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { reportKeyItemHistory } from "../../api/threework";

export default {
  props: [
    "ddheight",
    "leadsData",
    "dutysData",
    "isEditShow",
    "noEdit",
    "ruleList",
    "nodeList",
    "basicInfo",
    "reportDetail",
    "workid",
    "histableHeight",
    "newReportList",
    "isCenter",
    "lastprogress"
  ],
  components: {},
  data() {
    return {
      addItemData: {},
      itemTitle: "",
      itemVisible: false,
      readOn: false,
      changeNum: "",
      changeWord: "",
      tindex: 0,
      isHistoryShow: false, //
      isProgressShow: false, //
      historyData: [], //
      progressData: [], //
      isDetailShow: true,
      addLeadVisible: false,
      addDutyVisible: false,
      taskTitle: "",
      addLeadData: {},
      addDutyData: {},
      ruleVisible: false,
      ruleTitle: "",
      ruleBtn: "",
      addRuleForm: { cycletype: 1 }, //
      weekList: [
        { value: "1", name: "" },
        { value: "2", name: "" },
        { value: "3", name: "" },
        { value: "4", name: "" },
        { value: "5", name: "" },
        { value: "6", name: "" },
        { value: "7", name: "" }
      ],
      // 
      submitType: [
        { value: 1, type: "" },
        // { value: 2, type: "" },
        { value: 3, type: "" },
        { value: 4, type: "" },
        { value: 5, type: "" }
      ],
      // 
      timeday: [
        { value: "1", name: "1" },
        { value: "2", name: "2" },
        { value: "3", name: "3" },
        { value: "4", name: "4" },
        { value: "5", name: "5" },
        { value: "6", name: "6" },
        { value: "7", name: "7" },
        { value: "8", name: "8" },
        { value: "9", name: "9" },
        { value: "10", name: "10" },
        { value: "11", name: "11" },
        { value: "12", name: "12" },
        { value: "13", name: "13" },
        { value: "14", name: "14" },
        { value: "15", name: "15" },
        { value: "16", name: "16" },
        { value: "17", name: "17" },
        { value: "18", name: "18" },
        { value: "19", name: "19" },
        { value: "20", name: "20" },
        { value: "21", name: "21" },
        { value: "22", name: "22" },
        { value: "23", name: "23" },
        { value: "24", name: "24" },
        { value: "25", name: "25" },
        { value: "26", name: "26" },
        { value: "27", name: "27" },
        { value: "28", name: "28" },
        { value: "29", name: "29" },
        { value: "30", name: "30" },
        { value: "31", name: "31" }
      ],
      dateShow1: true, //
      dateShow2: false, //
      dateShow3: false, //
      dateShow4: false, //
      dateShow5: false, //
      region: "", //
      monthday: "", //
      quarter: "", //
      weekday: "", //
      daytime: "", //
      cycleNum: 1,
      ruleIndex: -1
    };
  },
  created() {},
  methods: {
    // 
    weekFormat(scope, value) {
      switch (value) {
        case "1":
          return "";
          break;
        case "2":
          return "";
          break;
        case "3":
          return "";
          break;
        case "4":
          return "";
          break;
        case "5":
          return "";
          break;
        case "6":
          return "";
          break;
        case "7":
          return "";
          break;
      }
    },
    nullFormat(scope, value) {
      if (value == "null" || value == null) {
        return "<span>/</span>";
      } else {
        return "<span>" + value + "</span>";
      }
    },
    // 
    stateFormat(scope, value) {
      switch (value) {
        case 2:
          return "";
          break;
        case 1:
          return "";
          break;
        case 0:
          return "";
          break;
      }
    },
    // 
    detailRuleBtn(index, row) {
      var ctype = row.cycletype;
      switch (ctype) {
        case 1:
          this.dateShow1 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.region = row.cycletime;
          break;
        case 2:
          this.dateShow2 = true;
          this.dateShow1 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.weekday = row.cycletime;
          break;
        case 3:
          this.dateShow3 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.monthday = row.cycletime;
          break;
        case 4:
          this.dateShow4 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow5 = false;
          this.quarter = row.cycletime;
          break;
        case 5:
          this.dateShow5 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow1 = false;
          this.daytime = row.cycletime;
          break;
      }

      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "detail";
      this.addRuleForm = Object.assign({}, row);
      this.addRuleForm.daytime = new Date(this.addRuleForm.daytime);
      this.addRuleForm.starttime = new Date(this.addRuleForm.starttime);
      this.addRuleForm.endtime = new Date(this.addRuleForm.endtime);
      this.ruleIndex = index;
    },
    // 
    ctypeFormat(scope, value) {
      switch (value) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
      }
    },
    // 
    cycleChange() {
      this.cycleNum = this.addRuleForm.cycletype;
      switch (this.cycleNum) {
        case 1:
          this.dateShow1 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 2:
          this.dateShow2 = true;
          this.dateShow1 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 3:
          this.dateShow3 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 4:
          this.dateShow4 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow5 = false;
          break;
        case 5:
          this.dateShow5 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow1 = false;
          break;
      }
    },

    timestampToTime(row, column) {
      var date = new Date(row.stoptime);
      return row.stoptime | dateformat("YYYY-MM-DD HH:mm:ss");
    },
    enddateToTime(row, column) {
      var date = new Date(row.endtime);
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      return Y + M + D;
    },

    timestampToTime2(row, column) {
      var date = new Date(row.createtime);
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate();
      return Y + M + D;
    },
    toEdit() {
      this.$emit("edit-event");
    },
    // 
    formstatusFormat(scope, value) {
      switch (value) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case -1:
          return "";
          break;
      }
    },
    // -----------------------------------------
    // 
    handleDetail2(index, row) {
      this.changeNum = "read";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = true;
      this.addLeadVisible = true;
      this.addLeadData = Object.assign({}, row);
    },
    // -
    submitLead: function() {
      this.addLeadVisible = false;
    },
    // 

    // -----------------------------------------
    // 
    handleDetail3(index, row) {
      this.changeNum = "read";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = true;
      this.addDutyVisible = true;
      this.addDutyData = Object.assign({}, row);
    },
    // -
    submitDuty: function() {
      this.addDutyVisible = false;
    },
    // -
    returnDetail() {
      this.isDetailShow = true;
      this.isHistoryShow = false;
    },
    // -
    returnDetail2() {
      this.isDetailShow = true;
      this.isProgressShow = false;
    },
    // 
    lookProgress() {
      let para = { situationid: this.workid };
      reportKeyItemHistory(para, data => {
        this.progressData = data.data.obj;
        var arr = [];
        for (var i = 0; i < this.progressData.length; i++) {
          var files = this.progressData[i].filepath;
          var filearr = [];
          if (files == null) {
            filearr = [];
          } else {
            arr = files.split(",");
            var ll = arr.length;
            if (arr[ll - 1] == "") {
              arr.splice(ll - 1, 1);
            }
            var arr2 = arr;
            for (var j = 0; j < arr2.length; j++) {
              var obj = {};
              obj.url = this.imgIp + arr2[j];
              obj.name = arr2[j];
              filearr.push(obj);
            }
          }
          this.progressData[i].filepath = filearr;
        }
      });
      this.isDetailShow = false;
      this.isProgressShow = true;
    },

    goback() {
      if (this.isCenter == 1) {
        this.$router.push({
          path: "/index"
        });
      } else if (this.isCenter == 2) {
        this.$router.push({
          path: "/personCenter"
        });
      } else {
        this.$emit("my-event");
      }
    },
    // 
    statusFormat(scope, value) {
      switch (value) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case -1:
          return "";
          break;
      }
    },
    // 
    cellClassName({ row, column, rowIndex }) {
      let status = row.status;
      switch (status) {
        case 1:
          return "yellow";
          break;
        case 2:
          return "red";
          break;
        case 3:
          return "zh";
          break;
        case 4:
          return "zhou";
          break;
        case -1:
          return "gray";
          break;
          return "";
      }
    }
  }
};
</script>

<style lang="scss">
.addplan {
  .speed {
    .pbox {
      display: flex;
      margin-bottom: 10px;
      .tt {
        color: #333;
        font-size: 14px;
        width: 110px;
        text-align: right;
        line-height: 2;
        padding-top: 3px;
        padding-right: 5px;
      }
      .conbox {
        flex: 1;
        color: #333;
        font-size: 14px;
        line-height: 2;
        border: 1px solid #dcdfe6;
        padding: 3px 10px;
        border-radius: 3px;
        min-height: 64px;
      }
      .conbox2 {
        min-height: 36px;
      }
    }
  }
  .filelist {
    padding: 0 30px;
    p {
      margin-bottom: 10px;
      font-size: 14px;
      color: #222;
      font-weight: bold;
    }
    .list {
      a {
        color: #1890ff;
        font-size: 14px;
        line-height: 2;
      }
      .not {
        margin-top: 10px;
        font-weight: normal;
      }
    }
  }
  .pbtn {
    margin-bottom: 20px;
  }
  .box {
    margin-bottom: 30px;
    .addbtn {
      margin-bottom: 10px;
    }
  }
  .history {
    .title {
      color: #222;
      font-size: 16px;
      font-weight: bold;
      margin: 0 0 15px;
    }
    .hbtns {
      margin: 10px 0;
    }
    .tableinfo {
      .filetitle {
        display: block;
        color: #1890ff;
        font-size: 13px;
      }
    }
  }
  .boxcontent {
    padding: 20px;
    .lastitem {
      margin-bottom: 0;
    }
    .label-style {
      .el-form-item__label {
        line-height: 1.4;
      }
    }
    .addwrap {
      display: flex;
      .addbox {
        margin-right: 30px;
      }
    }
    .addbox {
      .jiabtn {
        height: 36px;
        line-height: 36px;
        position: relative;
        display: inline-block;
        .icon {
          color: #666;
          font-size: 24px;
          display: inline-block;
          height: 36px;
          line-height: 36px;
          cursor: pointer;
        }
        .awrap {
          position: absolute;
          left: 44px;
          top: 0;
          border: 1px solid #ccc;
          background: #fff;
          width: 250px;
          padding: 5px 10px 10px;
          z-index: 99;
          p {
            color: #666;
          }
          .el-input__inner {
            width: 100%;
            margin-bottom: 10px;
          }
          .buttons {
            display: flex;
            justify-content: center;
            .el-button {
              padding: 8px 18px;
            }
          }
        }
      }
    }
  }
  .plan-dialog {
    .abox {
      .el-input-group__append {
        padding: 0 8px;
        background: #fff;
        border: 0;
        color: #222;
      }
      .el-input--prefix .el-input__inner {
        width: 200px;
      }
      .el-date-editor.el-input,
      .el-date-editor.el-input__inner {
        width: 200px;
      }
      .el-input__inner {
        width: 200px;
      }
    }
    .el-form--inline {
      .abox2 {
        .el-form-item {
          display: block;
        }
        .el-form-item__content {
          .el-textarea {
            width: 535px;
          }
        }
        .el-form-item__label {
          line-height: 1.8;
        }
      }
    }
  }
  .goback {
    padding: 50px 0;
    display: flex;
    justify-content: flex-end;
    .el-button {
      padding: 10px 30px;
    }
    .el-button + .el-button {
      margin-left: 20px;
    }
  }
}
// 
.rule-dialog {
  .el-input-group__append,
  .el-input-group__prepend {
    color: #222;
    background: transparent;
    border: 0;
    padding: 0 5px;
  }
  .el-form-item {
    .el-input {
      width: 220px;
    }
  }
  .day {
    .el-input {
      width: 150px;
    }
  }
}
.jibeninfo {
  display: flex;
  align-items: stretch;
  fieldset {
    height: 100%;
  }
}
</style>



