<template>
  <div class="addplan">
    <div class="pbtn">
      <el-button @click="goback" size="small" type="primary"> </el-button>
    </div>
    <div class="addcontent" :style="addheight">
      <el-row class="box jibeninfo" :gutter="30">
        <el-col :span="12">
          <fieldset>
            <legend></legend>
            <div class="boxcontent editplanwrap">
              <el-form label-width="120px" size="medium" ref="basicInfo" :model="basicInfo">
                <div class="addbox">
                  <el-form-item
                    label=""
                    prop="yearid"
                    :rules="[{ required: true, message: '', trigger: 'change' }]"
                  >
                    <el-select
                      v-model="basicInfo.yearid"
                      filterable
                      clearable
                      placeholder=""
                      @focus="getyear"
                      @change="selectChange"
                    >
                      <el-option
                        v-for="item in years"
                        :key="item.id"
                        :label="item.year"
                        :value="item.id"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <div class="jiabtn">
                    <i class="icon el-icon-circle-plus" @click="yearShow = true"></i>
                    <div class="awrap" v-if="yearShow">
                      <p></p>
                      <el-input placeholder="" v-model="yearname"></el-input>
                      <div class="buttons">
                        <el-button @click="yearShow = false"></el-button>
                        <el-button @click="newYear" type="primary"></el-button>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="addbox">
                  <el-form-item
                    label=""
                    prop="workname"
                    :rules="[{ required: true, message: '', trigger: 'change' }]"
                  >
                    <el-input placeholder="" v-model="basicInfo.workname"></el-input>
                  </el-form-item>
                </div>
                <div class="addbox">
                  <el-form-item label="" prop="nodeplan">
                    <el-input
                      rows="3"
                      type="textarea"
                      placeholder=""
                      v-model="basicInfo.nodeplan"
                    ></el-input>
                  </el-form-item>
                </div>
                <div class="addbox">
                  <el-form-item label="" prop="arealeadids" class="ww1">
                    <el-select
                      v-model="basicInfo.arealeadids"
                      multiple
                      clearable
                      collapse-tags
                      placeholder=""
                      @change="selectChange"
                    >
                      <el-option
                        v-for="item in leaderlist"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </div>
                <div class="addbox">
                  <el-form-item label="" prop="secondwork">
                    <el-select
                      v-model="basicInfo.secondwork"
                      multiple
                      clearable
                      placeholder=""
                      @focus="getSeerList()"
                    >
                      <el-option
                        v-for="item in seeunitList"
                        :key="item.id"
                        :label="item.name"
                        :value="item.name"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                  <!-- <div class="jiabtn">
                    <i class="icon el-icon-circle-plus" @click="unitShow = true"></i>
                    <div class="awrap" v-if="unitShow">
                      <p></p>
                      <el-input placeholder="" v-model="unitname"></el-input>
                      <div class="buttons">
                        <el-button @click="unitShow = false"></el-button>
                        <el-button @click="newUnit" type="primary"></el-button>
                      </div>
                    </div>
                  </div> -->
                </div>
              </el-form>
            </div>
          </fieldset>
        </el-col>
        <el-col :span="12">
          <fieldset style="height:100%;">
            <legend></legend>
            <div class="boxcontent">
              <div class="speed">
                <div class="pbox">
                  <p class="tt"></p>
                  <div class="conbox">
                    <!-- <p v-for="(item, index) in lastprogress" :key="index">{{item.question}}</p> -->
                    <p>{{lastprogress}}</p>
                  </div>
                </div>
                <div class="pbox">
                  <p class="tt"></p>
                  <div class="conbox">
                    <p v-for="(item, index) in newReportList" :key="index">{{item.question}}</p>
                  </div>
                </div>
                <div class="pbox">
                  <p class="tt"></p>
                  <div class="conbox">
                    <p v-for="(item, index) in newReportList" :key="index">{{item.wenti}}</p>
                  </div>
                </div>
                <div class="pbox">
                  <p class="tt"></p>
                  <div class="conbox">
                    <p v-for="(item, index) in newReportList" :key="index">{{item.measure}}</p>
                  </div>
                </div>
                <div class="filelist">
                  <p class="tt"></p>
                  <div class="list">
                    <div>
                      <div v-for="(item, index) in newReportList" :key="index">
                        <div v-for="(sitem, kk) in item.newFiles" :key="kk">
                          <a
                            :href="sitem.url"
                            target="_blank"
                            style="text-align:left;"
                            class="filetitle"
                          >{{index+1}}. {{sitem.name}}</a>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </fieldset>
        </el-col>
      </el-row>

      <el-row class="box">
        <!--  -->
        <el-col :span="24">
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addRuleBtn" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="ruleList"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                :cell-class-name="cellClassName"
                height="200px"
              >
                <el-table-column prop="stagename" label=""></el-table-column>
                <el-table-column prop="cycletype" label="">
                  <template slot-scope="scope">
                    <span v-html="ctypeFormat(scope,scope.row.cycletype)"></span>
                  </template>
                </el-table-column>
                <el-table-column prop="cycletime" label="">
                  <template slot-scope="scope">
                    <!--  -->
                    <p v-if="scope.row.cycletype==1">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <p v-if="scope.row.cycletype==2">
                      <span v-html="weekFormat(scope,scope.row.cycletime)"></span>
                    </p>
                    <!--  -->
                    <p v-else-if="scope.row.cycletype==3">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <p v-else-if="scope.row.cycletype==4">{{scope.row.cycletime}}</p>
                    <!--  -->
                    <p v-else-if="scope.row.cycletype==5">{{scope.row.cycletime}}</p>
                  </template>
                </el-table-column>
                <el-table-column prop="warnday" label="()"></el-table-column>
                <el-table-column prop="starttime" label="">
                  <template slot-scope="scope">
                    <span>{{scope.row.starttime | dateformat('YYYY-MM-DD')}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="endtime" :formatter="endtimeToTime" label=""></el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    <el-button class="btn" @click="editRuleBtn(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="delRuleBtn(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
      </el-row>
      <!--  -->
      <el-row class="box" :gutter="30">
        <el-col :span="12">
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addLead" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="leadsData"
                highlight-current-row
                style="width: 100%;"
                class="tableinfo"
                height="250px"
                :cell-class-name="cellClassName"
              >
                <el-table-column prop="deptName" label=""></el-table-column>
                <el-table-column prop="leadername" label=""></el-table-column>
                <el-table-column prop="agentname" label=""></el-table-column>
                <el-table-column prop="formstatus" label="">
                  <template slot-scope="scope">
                    <div>
                      <el-tag size="small">
                        <i
                          class="fa mark"
                          v-if="scope.row.formstatus==1"
                          style="background:#fbe804;"
                        ></i>
                        <i
                          class="fa mark"
                          v-else-if="scope.row.formstatus==2"
                          style="background:#f00;"
                        ></i>
                        <i
                          class="fa mark"
                          v-else-if="scope.row.formstatus== -1"
                          style="background:#999;"
                        ></i>
                        <i class="fa mark" v-else></i>
                        <span v-html="formstatusFormat(scope,scope.row.formstatus)"></span>
                      </el-tag>
                    </div>
                  </template>
                </el-table-column>
                <el-table-column label="" width="120">
                  <template scope="scope">
                    <el-button class="btn" @click="handleEdit2(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="handleDelete2(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
        <el-col :span="12">
          <fieldset>
            <legend></legend>
            <div class="boxcontent">
              <el-button class="addbtn" @click="addDuty" size="small" type="primary"> </el-button>
              <el-table
                border
                :data="dutysData"
                highlight-current-row
                style="width: 100%;"
                height="250px"
                class="tableinfo"
                :cell-class-name="cellClassName"
              >
                <el-table-column prop="deptName" label=""></el-table-column>
                <el-table-column prop="leadername" label=""></el-table-column>
                <el-table-column prop="agentname" label=""></el-table-column>
                <el-table-column label="" width="120">
                  <template scope="scope">
                    <el-button class="btn" @click="handleEdit3(scope.$index, scope.row)"></el-button>
                    <el-button class="btn" @click="handleDelete3(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </fieldset>
        </el-col>
      </el-row>
    </div>
    <div class="goback">
      <el-button @click="goback" size="small" type="warning"> </el-button>
      <el-button @click="submitAll" type="primary" size="small"> </el-button>
    </div>

    <!--  -->
    <el-dialog :title="ruleTitle" :visible.sync="ruleVisible" width="700px" class="rule-dialog">
      <el-form
        :model="addRuleForm"
        label-width="93px"
        ref="addRuleForm"
        size="small"
        :inline="true"
        :append-to-body="true"
      >
        <el-form-item label="" prop="stagename">
          <el-input v-model="addRuleForm.stagename" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" class="day" prop="warnday">
          <el-input v-model="addRuleForm.warnday">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="cycletype">
          <el-select
            v-model="addRuleForm.cycletype"
            clearable
            placeholder=""
            @change="cycleChange()"
          >
            <el-option
              v-for="item in submitType"
              :key="item.value"
              :label="item.type"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>

        <!-- 1  -->
        <el-form-item label="" class="day" prop="region" v-if="dateShow1">
          <el-input v-model="region">
            <template slot="append"></template>
          </el-input>
        </el-form-item>
        <!-- 2  -->
        <el-form-item label="" prop="weekday" v-if="dateShow2">
          <el-select v-model="weekday" clearable placeholder="">
            <el-option
              v-for="item in weekList"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 3  -->
        <el-form-item label="" prop="monthday" v-if="dateShow3">
          <el-select v-model="monthday" clearable placeholder="">
            <el-option
              v-for="item in timeday"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 4  -->
        <el-form-item label="" prop="quarter" v-if="dateShow4">
          <el-select v-model="quarter" clearable placeholder="">
            <el-option
              v-for="item in timeday"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- 5  -->
        <el-form-item label="" prop="daytime" v-if="dateShow5">
          <el-date-picker
            v-model="daytime"
            format="yyyy-MM-dd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>

        <el-form-item
          label=""
          prop="starttime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="addRuleForm.starttime"
            format="yyyy-MM-dd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label=""
          prop="endtime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="addRuleForm.endtime"
            format="yyyy-MM-dd"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitRule" type="primary">{{ruleBtn}}</el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="taskTitle"
      :visible.sync="addLeadVisible"
      width="800px"
      class="task-dialog task-dialog2"
    >
      <el-form
        :inline="true"
        :model="addLeadData"
        label-width="100px"
        ref="addLeadData"
        :append-to-body="true"
      >
        <div class="leabox">
          <el-form-item
            label=""
            prop="deptid"
            :rules="[{ required: true, message: '', trigger: 'change' }]"
          >
            <el-select
              v-model="addLeadData.deptid"
              clearable
              filterable
              placeholder=""
              @change="deptChange"
            >
              <el-option
                v-for="item in deptlist"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="" prop="leadername">
          <el-input v-model="addLeadData.leadername"></el-input>
        </el-form-item>
        <el-form-item label="" prop="leaderphone">
          <el-input v-model="addLeadData.leaderphone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentname">
          <el-input v-model="addLeadData.agentname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentphone">
          <el-input v-model="addLeadData.agentphone"></el-input>
        </el-form-item>
        <div class="leabox leabox2">
          <el-form-item label="" prop="duty">
            <el-input type="textarea" :autosize="{ minRows: 10}" v-model="addLeadData.duty"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="submitLead" type="primary">{{changeWord}}</el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :title="taskTitle"
      :visible.sync="addDutyVisible"
      width="800px"
      class="task-dialog task-dialog2"
    >
      <el-form
        :inline="true"
        :model="addDutyData"
        label-width="100px"
        ref="addDutyData"
        :append-to-body="true"
      >
        <div class="leabox">
          <el-form-item
            label=""
            prop="deptid"
            :rules="[{ required: true, message: '', trigger: 'change' }]"
          >
            <el-select
              v-model="addDutyData.deptid"
              @change="deptChange2"
              clearable
              filterable
              :readonly="readOn"
              placeholder=""
            >
              <el-option
                v-for="item in deptlist"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="" prop="leadername">
          <el-input v-model="addDutyData.leadername"></el-input>
        </el-form-item>
        <el-form-item label="" prop="leaderphone">
          <el-input v-model="addDutyData.leaderphone" :readonly="readOn"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentname">
          <el-input v-model="addDutyData.agentname" :readonly="readOn"></el-input>
        </el-form-item>
        <el-form-item label="" prop="agentphone">
          <el-input v-model="addDutyData.agentphone" :readonly="readOn"></el-input>
        </el-form-item>
        <div class="leabox leabox2">
          <el-form-item label="" prop="duty">
            <el-input
              type="textarea"
              :autosize="{ minRows: 10}"
              :readonly="readOn"
              v-model="addDutyData.duty"
            ></el-input>
          </el-form-item>
        </div>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="submitDuty" type="primary">{{changeWord}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  getYear,
  addYear,
  threeworkList,
  getAllPlanlist,
  checkReportRules,
  seerList
} from "../../api/decisionApi";
import {
  editKeywork,
  oneLevelList,
  keyLevelList,
  changeKeyworkFormstate,
  synchKeyworkFormstate
} from "../../api/threework";
export default {
  props: [
    "leaderlist",
    "deptlist",
    "basicInfo",
    "addheight",
    "leadsData",
    "dutysData",
    "ruleList",
    "urlIp",
    "upfile",
    "workid",
    "newReportList",
    "lastprogress",
    "years"
  ],
  components: {},
  data() {
    return {
      userid: JSON.parse(this.$store.getters.userdata).id,
      checkAll: false,
      checkedItems: [],
      allitems: [],
      isIndeterminate: false,
      choosenum: 0,
      hasitem: [],
      // end
      // years: [],
      yearShow: false,
      yearname: "",
      bitemShow: false,
      sitemShow: false,
      threeShow: false,
      fourShow: false,
      unitShow: false,
      bitemname: "",
      sitemname: "",
      fourname: "",
      threename: "",
      unitname: "", //
      addItemData: {},
      itemTitle: "",
      itemVisible: false,
      readOn: false,
      changeNum: "",
      changeWord: "",
      tindex: -1,
      biglist: [],
      minlist: [],
      readOn: false,
      addLeadData: {},
      addDutyData: {},
      taskTitle: "",
      ruleVisible: false,
      dateShow1: false, //
      dateShow2: false, //
      dateShow3: false, //
      dateShow4: false, //
      dateShow5: false, //
      region: "", //
      monthday: "", //
      quarter: "", //
      weekday: "", //
      daytime: "", //
      ruleTitle: "",
      ruleBtn: "",
      cycleNum: 0,
      addRuleForm: { cycletype: 1 }, //
      weekList: [
        { value: "1", name: "" },
        { value: "2", name: "" },
        { value: "3", name: "" },
        { value: "4", name: "" },
        { value: "5", name: "" },
        { value: "6", name: "" },
        { value: "7", name: "" }
      ],
      // 
      submitType: [
        { value: 1, type: "" },
        // { value: 2, type: "" },
        { value: 3, type: "" },
        { value: 4, type: "" },
        { value: 5, type: "" }
      ],
      // 
      timeday: [
        { value: "1", name: "1" },
        { value: "2", name: "2" },
        { value: "3", name: "3" },
        { value: "4", name: "4" },
        { value: "5", name: "5" },
        { value: "6", name: "6" },
        { value: "7", name: "7" },
        { value: "8", name: "8" },
        { value: "9", name: "9" },
        { value: "10", name: "10" },
        { value: "11", name: "11" },
        { value: "12", name: "12" },
        { value: "13", name: "13" },
        { value: "14", name: "14" },
        { value: "15", name: "15" },
        { value: "16", name: "16" },
        { value: "17", name: "17" },
        { value: "18", name: "18" },
        { value: "19", name: "19" },
        { value: "20", name: "20" },
        { value: "21", name: "21" },
        { value: "22", name: "22" },
        { value: "23", name: "23" },
        { value: "24", name: "24" },
        { value: "25", name: "25" },
        { value: "26", name: "26" },
        { value: "27", name: "27" },
        { value: "28", name: "28" },
        { value: "29", name: "29" },
        { value: "30", name: "30" },
        { value: "31", name: "31" }
      ],
      ruleIndex: -1,
      fileList: [],
      fileArr: [],
      fouritemList: [], //
      seeunitList: [], //
      addLeadVisible: false,
      addDutyVisible: false
    };
  },
  created() {
    this.getyear();
    this.getBiglist();
    this.getbigterm();
  },
  methods: {
    // 
    weekFormat(scope, value) {
      switch (value) {
        case "1":
          return "";
          break;
        case "2":
          return "";
          break;
        case "3":
          return "";
          break;
        case "4":
          return "";
          break;
        case "5":
          return "";
          break;
        case "6":
          return "";
          break;
        case "7":
          return "";
          break;
      }
    },
    // 
    getAllDatas() {
      getAllPlanlist({ typemodel: this.typemodel }, data => {
        if (data.data.obj == null) {
          this.allitems = [];
        } else {
          this.allitems = data.data.obj;
          for (var i = 0; i < this.allitems.length; i++) {
            if (this.allitems[i].id == this.workid) {
              // 
              this.allitems.splice(i, 1);
              break;
            }
          }
        }
      });
    },
    // 
    handleCheckAllChange(val) {
      let arr = [];
      for (var i = 0; i < this.allitems.length; i++) {
        arr.push(this.allitems[i].id);
      }
      this.choosenum = val ? arr.length : 0;
      this.checkedItems = val ? arr : [];
      this.isIndeterminate = false;
    },
    handleCheckedItemsChange(value) {
      this.choosenum = value.length;
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.allitems.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.allitems.length;
    },
    //  end
    // 
    removeArr(arr, value) {
      var i = arr.length;
      while (i--) {
        if (arr[i] === value) {
          return i;
        }
      }
      return false;
    },
    //  
    handleRemove(file, fileList) {
      let name = file.response.obj;
      this.fileArr.splice(this.removeArr(this.fileArr, name), 1);
    },
    // 
    handlePreview(file) {},
    //  
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
    //  
    handleSuccess(file, fileList) {
      let name = file.obj;
      this.fileArr.push(name);
    },

    // 
    getThreeworkList() {
      if (
        this.basicInfo.yearid == null ||
        this.basicInfo.bigid == null ||
        this.basicInfo.minid == null
      ) {
        this.$message({
          message: "",
          type: "warning"
        });
      } else {
        let para = {
          bigid: this.basicInfo.bigid,
          yearid: this.basicInfo.yearid,
          minid: this.basicInfo.minid,
          typemodel: this.typemodel
        };
        threeworkList(para, data => {
          this.threeitemList = data.data.obj;
        });
      }
    },
    // 
    newThree() {
      if (
        this.basicInfo.yearid == null ||
        this.basicInfo.bigid == null ||
        this.basicInfo.minid == null
      ) {
        this.$message({
          message: "",
          type: "warning"
        });
      } else {
        let para = {
          bigid: this.basicInfo.bigid,
          yearid: this.basicInfo.yearid,
          minid: this.basicInfo.minid,
          partname: this.threename,
          typemodel: this.typemodel
        };
        addThreework(para, data => {
          this.threeShow = false;
          this.$message({
            message: "",
            type: "success"
          });
          this.getThreeworkList();
          this.threename = "";
        });
      }
    },
    // 
    getSeerList() {
      seerList("", data => {
        this.seeunitList = data.data.obj;
      });
    },
    // 
    newUnit() {
      let para = { name: this.unitname };
      addSeer(para, res => {
        this.unitShow = false;
        this.$message({
          message: "",
          type: "success"
        });
        this.unitname = "";
        this.getSeerList();
      });
    },
    endtimeToTime(row, column) {
      var date = new Date(row.endtime); //10*1000131000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate();
      return Y + M + D;
    },
    // 
    ctypeFormat(scope, value) {
      this.$forceUpdate();
      switch (value) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case 5:
          return "";
          break;
      }
    },
    // 
    cycleChange() {
      this.cycleNum = this.addRuleForm.cycletype;
      switch (this.cycleNum) {
        case 1:
          this.dateShow1 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 2:
          this.dateShow2 = true;
          this.dateShow1 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 3:
          this.dateShow3 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          break;
        case 4:
          this.dateShow4 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow5 = false;
          break;
        case 5:
          this.dateShow5 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow1 = false;
          break;
      }
    },
    // 
    addRuleBtn() {
      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "add";
      this.addRuleForm = { cycletype: 1 };
      this.cycleChange();
    },
    // 
    addRuleSubmit() {
      this.$refs.addRuleForm.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addRuleForm);
          this.cycleNum = this.addRuleForm.cycletype;
          if (this.cycleNum == 1) {
            para.cycletime = this.region; //
          } else if (this.cycleNum == 2) {
            para.cycletime = this.weekday; //
          } else if (this.cycleNum == 3) {
            para.cycletime = this.monthday; //
          } else if (this.cycleNum == 4) {
            para.cycletime = this.quarter; //
          } else if (this.cycleNum == 5) {
            para.cycletime = this.daytime; //
          }
          para.typemodel = this.typemodel;
          this.ruleList.push(para);
          this.$refs["addRuleForm"].resetFields();
          this.ruleVisible = false;
          this.ruleTitle = "";
          this.ruleBtn = "";
          this.changeNum = "";
          this.cycleNum = 0;
        } else {
          return false;
        }
      });
    },
    // 
    editRuleBtn(index, row) {
      var ctype = row.cycletype;
      switch (ctype) {
        case 1:
          this.dateShow1 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.region = row.cycletime;
          break;
        case 2:
          this.dateShow2 = true;
          this.dateShow1 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.weekday = row.cycletime;
          break;
        case 3:
          this.dateShow3 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow4 = false;
          this.dateShow5 = false;
          this.monthday = row.cycletime;
          break;
        case 4:
          this.dateShow4 = true;
          this.dateShow1 = false;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow5 = false;
          this.quarter = row.cycletime;
          break;
        case 5:
          this.dateShow5 = true;
          this.dateShow2 = false;
          this.dateShow3 = false;
          this.dateShow4 = false;
          this.dateShow1 = false;
          this.daytime = row.cycletime;
          break;
      }
      this.ruleVisible = true;
      this.ruleTitle = "";
      this.ruleBtn = "";
      this.changeNum = "edit";
      this.addRuleForm = Object.assign({}, row);
      this.addRuleForm.daytime = new Date(this.addRuleForm.daytime);
      this.addRuleForm.starttime = new Date(this.addRuleForm.starttime);
      this.addRuleForm.endtime = new Date(this.addRuleForm.endtime);
      this.ruleIndex = index;
    },
    // 
    editRuleSubmit() {
      this.$refs.addRuleForm.validate(valid => {
        if (valid) {
          this.cycleNum = this.addRuleForm.cycletype;
          let para = Object.assign({}, this.addRuleForm);
          if (this.cycleNum == 1) {
            para.cycletime = this.region; //
          } else if (this.cycleNum == 2) {
            para.cycletime = this.weekday; //
          } else if (this.cycleNum == 3) {
            para.cycletime = this.monthday; //
          } else if (this.cycleNum == 4) {
            para.cycletime = this.quarter; //
          } else if (this.cycleNum == 5) {
            para.cycletime = this.daytime; //
          }
          this.ruleList.splice(this.ruleIndex, 1, para);
          this.$refs["addRuleForm"].resetFields();
          this.ruleVisible = false;
          this.ruleIndex = -1;
          this.ruleTitle = "";
          this.ruleBtn = "";
          this.changeNum = "";
          this.cycleNum = 0;
        } else {
          return false;
        }
      });
    },
    // 
    delRuleBtn(index, row) {
      this.ruleList.splice(index, 1);
      this.ruleVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },
    // 
    submitRule() {
      if (this.changeNum == "add") {
        this.addRuleSubmit();
      } else {
        this.editRuleSubmit();
      }
    },
    // 
    formstatusFormat(scope, value) {
      switch (value) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case -1:
          return "";
          break;
      }
    },
    // -----------------------------------------
    // 
    addLead: function() {
      this.changeNum = "add";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addLeadVisible = true;
    },
    // 
    handleDetail2(index, row) {
      this.changeNum = "read";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = true;
      this.addLeadVisible = true;
      this.addLeadData = Object.assign({}, row);
    },
    // 
    handleEdit2(index, row) {
      this.changeNum = "edit";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addLeadVisible = true;
      this.addLeadData = Object.assign({}, row);
      this.tindex = index;
    },
    // -
    submitLead: function() {
      if (this.changeNum == "read") {
        this.addLeadVisible = false;
      } else if (this.changeNum == "add") {
        this.addLeadSubmit();
      } else {
        this.editLeadSubmit();
      }
    },
    deptChange() {
      let deptname = "";
      for (var i = 0; i < this.deptlist.length; i++) {
        if (this.deptlist[i].id == this.addLeadData.deptid) {
          deptname = this.deptlist[i].name;
          break;
        }
      }
      this.addLeadData.deptName = deptname;
    },
    // 
    addLeadSubmit: function() {
      this.$refs.addLeadData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addLeadData);
          para.typemodel = this.typemodel;
          this.leadsData.push(para);
          this.$refs["addLeadData"].resetFields();
          this.addLeadVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    editLeadSubmit: function() {
      this.$refs.addLeadData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addLeadData);
          this.leadsData.splice(this.tindex, 1, para);
          this.$refs["addLeadData"].resetFields();
          this.addLeadVisible = false;
        } else {
          console.log("error");
          return false;
        }
      });
    },
    // 
    handleDelete2(index, row) {
      this.leadsData.splice(index, 1);
      this.$refs["addLeadData"].resetFields();
      this.addLeadVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },

    // -----------------------------------------
    // 
    addDuty: function() {
      this.changeNum = "add";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addDutyVisible = true;
    },
    // 
    handleDetail3(index, row) {
      this.changeNum = "read";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = true;
      this.addDutyVisible = true;
      this.addDutyData = Object.assign({}, row);
    },
    // 
    handleEdit3(index, row) {
      this.changeNum = "edit";
      this.changeWord = "";
      this.taskTitle = "";
      this.readOn = false;
      this.addDutyVisible = true;
      this.addDutyData = Object.assign({}, row);
      this.tindex = index;
    },
    // -
    submitDuty: function() {
      if (this.changeNum == "read") {
        this.addDutyVisible = false;
      } else if (this.changeNum == "add") {
        this.addDutySubmit();
      } else {
        this.editDutySubmit();
      }
    },
    deptChange2() {
      let deptname = "";
      for (var i = 0; i < this.deptlist.length; i++) {
        if (this.deptlist[i].id == this.addDutyData.deptid) {
          deptname = this.deptlist[i].name;
          break;
        }
      }
      this.addDutyData.deptName = deptname;
    },
    // 
    addDutySubmit: function() {
      this.$refs.addDutyData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addDutyData);
          para.typemodel = this.typemodel;
          this.dutysData.push(para);
          this.$refs["addDutyData"].resetFields();
          this.addDutyVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    editDutySubmit: function() {
      this.$refs.addDutyData.validate(valid => {
        if (valid) {
          let para = Object.assign({}, this.addDutyData);
          this.dutysData.splice(this.tindex, 1, para);
          this.$refs["addDutyData"].resetFields();
          this.addDutyVisible = false;
        } else {
          return false;
        }
      });
    },
    // 
    handleDelete3(index, row) {
      this.dutysData.splice(index, 1);
      this.$refs["addDutyData"].resetFields();
      this.addDutyVisible = false;
      this.$message({
        message: "",
        type: "success"
      });
    },
    selectChange() {
      this.$forceUpdate();
    },
    timestampToTime(row, column) {
      var date = new Date(row.submittime); //10*1000131000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      return Y + M + D;
    },
    goback() {
      this.$confirm(
        "<span style='color:#f00;'></span>",
        "",
        {
          dangerouslyUseHTMLString: true,
          type: "warning",
          center: true,
          modal: true,
          confirmButtonText: "",
          cancelButtonText: ""
        }
      )
        .then(() => {
          this.$emit("my-event");
          this.getyear();
        })
        .catch(() => {
          return false;
        });
    },
    // 
    getyear() {
      getYear("", data => {
        this.years = data.data.obj;
      });
    },
    // 
    newYear() {
      let para = { year: this.yearname };
      addYear(para, res => {
        this.yearShow = false;
        this.$message({
          message: "",
          type: "success"
        });
        this.getyear();
        this.yearname = "";
      });
    },
    // 
    getbigterm() {
      keyLevelList("", data => {
        this.biglist = data.data.obj;
      });
    },
    // 
    addBigSubmit() {
      this.biglist.push({ workname: this.bitemname });
      this.bitemShow = false;
      this.$message({
        message: "",
        type: "success"
      });
    },
    // 
    getBiglist() {
      oneLevelList("", data => {
        this.minlist = data.data.obj;
      });
    },
    // 
    addSubterm() {
      this.minlist.push({ nodeplan: this.sitemname });
      this.sitemShow = false;
      this.$message({
        message: "",
        type: "success"
      });
      this.sitemname = "";
    },
    // 
    statusFormat(scope, value) {
      switch (value) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
        case 4:
          return "";
          break;
        case -1:
          return "";
          break;
      }
    },
    // 
    cellClassName({ row, column, rowIndex }) {
      let status = row.status;
      switch (status) {
        case 1:
          return "yellow";
          break;
        case 2:
          return "red";
          break;
        case 3:
          return "zh";
          break;
        case 4:
          return "zhou";
          break;
        case -1:
          return "gray";
          break;
          return "";
      }
    },
    tableRowClassName({ row, rowIndex }) {
      if (row.status == -1) {
        return "noedit";
      } else if (row.status == 3) {
        return "lagbtn";
      }
    },

    //  --------------
    submitAll: function() {
      this.$refs.basicInfo.validate(valid => {
        if (valid) {
          // id
          var aids = this.basicInfo.arealeadids;
          if (aids.length == 0) {
            aids = "";
          } else {
            if (aids.length == 1) {
              aids = aids[0] + ",";
            } else {
              aids = aids.join(",");
            }
          }

          // 
          var year = "";
          for (var n = 0; n < this.years.length; n++) {
            if (this.basicInfo.yearid == this.years[n].id) {
              year = this.years[n].year;
              break;
            }
          }

          var secondwork = this.basicInfo.secondwork.join(',')

          var tripleWorkProgress = {
            id: this.workid,
            yearid: this.basicInfo.yearid,
            workname: this.basicInfo.workname,
            nodeplan: this.basicInfo.nodeplan,
            secondwork: secondwork,
            createuser: this.userid
          };
          tripleWorkProgress.dutyleaderid = aids;
          tripleWorkProgress.year = year;
          let opara = { tripleWorkProgress: tripleWorkProgress };
          opara.tripleWorkProgressLeadDeptList = this.leadsData;
          opara.tripleWorkProgressDutyDeptList = this.dutysData;
          opara.tripleWorkProgressRuleList = this.ruleList;
          opara = JSON.stringify(opara);
          editKeywork(opara, res => {
            let cpara = { workid: this.workid };
            changeKeyworkFormstate(cpara, data => {
              synchKeyworkFormstate({ workid: this.workid }, ress => {
                this.$emit("my-event");
                this.$emit("add-event");
                this.$message({
                  message: "",
                  type: "success"
                });
              });
            });
          });
        }
      });
    }
  }
};
</script>

<style lang="scss">
.addplan {
  .el-form.el-form--inline {
    display: block;
    .addbox {
      margin-right: 0;
    }
  }
  .pbtn {
    margin-bottom: 20px;
  }
  .box {
    margin-bottom: 30px;
    .addbtn {
      margin-bottom: 10px;
    }
  }
  .boxcontent {
    padding: 20px;
    .addwrap {
      display: flex;
    }
    .addbox {
      .jiabtn {
        height: 36px;
        line-height: 36px;
        position: relative;
        display: inline-block;
        .icon {
          color: #666;
          font-size: 24px;
          display: inline-block;
          height: 36px;
          line-height: 36px;
          cursor: pointer;
        }
        .awrap {
          position: absolute;
          left: 44px;
          top: 0;
          border: 1px solid #ccc;
          background: #fff;
          width: 250px;
          padding: 5px 10px 10px;
          z-index: 99;
          p {
            color: #666;
          }
          .el-input__inner {
            width: 100%;
            margin-bottom: 10px;
          }
          .buttons {
            display: flex;
            justify-content: center;
            .el-button {
              padding: 8px 18px;
            }
          }
        }
      }
    }
    .sbdate {
      .el-date-editor.el-input,
      .el-date-editor.el-input__inner {
        width: 100%;
      }
    }
    .tflexbox {
      display: flex;
      .el-form-item {
        flex: 1;
        margin-right: 10px;
      }
    }
  }
  .plan-dialog {
    .abox {
      .el-input-group__append {
        padding: 0 8px;
        background: #fff;
        border: 0;
        color: #222;
      }
      .el-input--prefix .el-input__inner {
        width: 200px;
      }
      .el-date-editor.el-input,
      .el-date-editor.el-input__inner {
        width: 200px;
      }
      .el-input__inner {
        width: 200px;
      }
    }
    .el-form--inline {
      .abox2 {
        .el-form-item {
          display: block;
        }
        .el-form-item__content {
          .el-textarea {
            width: 535px;
          }
        }
        .el-form-item__label {
          line-height: 1.8;
        }
      }
    }
  }
  .goback {
    padding: 30px 0 10px;
    display: flex;
    justify-content: flex-end;
    .el-button {
      padding: 10px 30px;
    }
    .el-button + .el-button {
      margin-left: 20px;
    }
  }
}
// 
.rule-dialog {
  .el-input-group__append,
  .el-input-group__prepend {
    color: #222;
    background: transparent;
    border: 0;
    padding: 0 5px;
  }
  .el-form-item {
    .el-input {
      width: 220px;
    }
  }
  .day {
    .el-input {
      width: 150px;
    }
  }
}
.jibeninfo {
  display: flex;
  align-items: stretch;
  fieldset {
    height: 100%;
  }
}

.task-dialog {
  .item-select {
    display: block;
    .el-select__tags {
      max-width: 100% !important;
      width: 100%;
      .el-input--suffix .el-input__inner {
        width: 100%;
      }
    }
  }
  .taskbox {
    display: flex;

    .day {
      .el-input__inner {
        width: 80px;
        border-radius: 4px;
      }
      .el-input-group__append {
        padding: 0 8px;
        background: #fff;
        border: 0;
        color: #333;
      }
    }
  }
  .taskbox2 {
    .el-input {
      width: 220px;
    }
  }
  .leabox {
    .el-form-item {
      width: 100%;
    }
  }
}
.task-dialog2 {
  .el-form--inline .el-form-item__content {
    width: 260px;
    .el-select {
      width: 100%;
    }
  }
  .leabox2 {
    .el-form-item__content {
      width: 634px;
    }
  }
}
.editplanwrap {
  .addbox {
    display: flex;
    .el-form-item {
      flex: 1;
      .el-select {
        display: block;
      }
    }
    .jiabtn {
      margin-left: 20px;
    }
  }
}
.speed {
  .pbox {
    display: flex;
    margin-bottom: 10px;
    .tt {
      color: #333;
      font-size: 14px;
      width: 110px;
      text-align: right;
      line-height: 2;
      padding-top: 3px;
      padding-right: 5px;
    }
    .conbox {
      flex: 1;
      color: #333;
      font-size: 14px;
      line-height: 2;
      border: 1px solid #dcdfe6;
      padding: 3px 10px;
      border-radius: 3px;
      min-height: 64px;
    }
    .conbox2 {
      min-height: 36px;
    }
  }
}
</style>



